<template>
	<div class="page2_3_1">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title1="绩效审计" title="计划目标比较疑点" id="more2_3_1" ydname="审计疑点线索挖掘"/>
			</div>
		</div>
		<div class="lineBox box-border-shadow">
			<div id="barChart" style="width: 100%;height: 100%;"></div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import reqData from "../../../reqData.js"
	import * as echarts from 'echarts';
	import interBtn from './../interactions'
	export default{
		name:'page2_3_1',
		data(){
			return{
				msg:["审计疑点线索挖掘","绩效审计","与计划目标比较"]
			}
		},
		components:{
			Place,
			interBtn
		},
		computed:{
			getOptionBar(){
				let option = {
					    tooltip: {
					        trigger: 'axis',
					        axisPointer: {
					            type: 'cross',
					            crossStyle: {
					                color: '#999'
					            }
					        }
					    },
					    toolbox: {
					        feature: {
					            dataView: {show: true, readOnly: false},
					            magicType: {show: true, type: ['line', 'bar']},
					            restore: {show: true},
					            saveAsImage: {show: true}
					        }
					    },
					    legend: {
					        data:['计划','实际']
					    },
					    xAxis: [
					        {
					            type: 'category',
					            data: ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月'],
					            axisPointer: {
					                type: 'shadow'
					            }
					        }
					    ],
					    yAxis: [
					        {
					            type: 'value',
					            name: '营业利润（万元）',
					            min: 0,
					            max: 250,
					            interval: 50,
					            axisLabel: {
					                formatter: '{value} 万元'
					            }
					        }
					    ],
					    series: [
					        {
					            name:'计划',
					            type:'bar',
					            data:[2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
					        },
					        {
					            name:'实际',
					            type:'bar',
					            data:[2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0, 2.3]
					        }
					    ]
					};
				return option;
			}
		},
		mounted(){
			let barChart = echarts.init(document.getElementById('barChart'));
			 barChart.setOption(this.getOptionBar);
			  $(window).resize(function(){
					barChart.resize({
						width:'auto',
						height:'auto'
					})
			  });
		}
		
	}
</script>
<style type="text/css" scoped="">
	.lineBox{
		padding: 3% 5%;
		box-sizing: border-box;
	    width:100%;
	    height: 800px;
	    margin: 20px auto;
	}
</style>


